body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.document-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: center;
          align-content: center;
  text-align: center;
}

.document-container table {
  width: 60%;
  margin: 30px auto;
}

.medical-process-container .set-done {
  border-radius: 50%;
  width: 25px;
  height: 25px;
  background: #646464;
  margin: 0 0 0 10px;
  padding: 0;
  color: white;
  font-size: 14px;
}

.medical-process-container .content {
  overflow-y: auto;
  height: 60vh;
}

.medical-process-container form {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 70px;
}

.medical-process-container form input,
.medical-process-container form select {
  margin-top: 10px;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.medical-process-container form textarea {
  margin: 0 20px;
  -webkit-flex: 3 1 300px;
          flex: 3 1 300px;
}

.medical-process-container ul {
  list-style: none;
  padding-left: 0;
}

.medical-process-container ul li {
  margin-bottom: 30px;
}

/* $container-color: rgb(161, 194, 188); */
.header {
  background: #929292;
  height: 100px;
  width: 100%;
  position: relative;
}

.header .radio-buttons {
  padding: 30px;
}

.header .log-out {
  position: absolute;
  top: 15px;
  right: 10px;
  margin: 15px;
  background: #e4ab6a;
  width: 200px;
}

ul {
  list-style: none;
}

a {
  color: black;
  text-decoration: none;
}

input,
select {
  margin: 20px;
  padding: 5px;
}

.container {
  background: #e4dcc4;
  width: 60%;
  height: 60vh;
  margin: 20px 6%;
  border-radius: 0 10px 10px 10px;
  padding: 20px 50px;
  box-shadow: 5px 5px 5px #585858;
}

button,
input[type="submit"] {
  padding: 10px 20px;
  border: 0;
  border-radius: 5px 5px;
  background: #e4ab6a;
  color: black;
  width: 200px;
  margin: 20px auto;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

button:hover,
input[type="submit"]:hover {
  background: #eb9a4e;
}

.login-container {
  margin: 70px auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: center;
          align-content: center;
  text-align: center;
  height: 67vh;
}

.login-container form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 30%;
  margin: 50px auto;
  margin-bottom: 10px;
}

.login-container button {
  margin-top: 0;
}

.register-container {
  margin: 15px auto;
  height: 85vh;
}

.register-container p,
.register-container h2 {
  text-align: center;
}

.register-container form {
  display: grid;
  width: 45%;
  margin: auto;
}

.register-container form span {
  padding: 7px;
  height: 45px;
}

.register-container form input,
.register-container form select {
  float: right;
  width: 200px;
  margin-top: 0;
}

.register-container form select {
  width: 210px;
}

.content {
  overflow-y: auto;
}

/* $container-color: rgb(161, 194, 188); */
/* .container {
    @include containerStyle;
} */
.documentation-container button {
  margin: 30px 40%;
}

.documentation-container .documents {
  text-align: left;
}

.documentation-container .filterForm {
  margin-top: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
}

.documentation-container .filterForm input, .documentation-container .filterForm select {
  margin: 0 10px;
  width: 120px;
}

.documentation-container ul {
  margin-top: 40px;
  padding-left: 0;
}

.documentation-container ul li {
  margin: 20px;
}

.recommendations-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.recommendations-container button {
  margin-bottom: 10px;
}

.recommendations-container a {
  margin: 10px auto;
  width: 200px;
}

.recommendation {
  padding: 10px 30px;
  background: #838383;
  text-align: left;
  cursor: pointer;
  color: white;
  box-shadow: 2px 2px 2px #333;
  width: 60%;
  margin: 5px auto 20px auto;
}

.form-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.form-container .submitButton {
  margin: 0;
}

.form-container form {
  display: grid;
  width: 50%;
  border: 1px solid black;
  padding: 30px 0px 0px 30px;
}

.form-container form label {
  width: 400px;
}

.form-container form label input {
  float: right;
  margin: 0 0 20px 0;
  width: 300px;
}

.form-container form label textarea {
  width: 310px;
  float: right;
  margin: 0;
  height: 70px;
}

.side-bar {
  position: absolute;
  top: 100px;
  right: 0;
  width: 18%;
  height: 100vh;
  background: #c9c9c9;
  padding: 20px;
}

.side-bar button {
  display: block;
}

.side-bar ul {
  list-style: none;
  padding-left: 0;
}

.side-bar ul input {
  margin: 10px;
}

.side-bar form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.side-bar form input {
  margin: 10px auto;
  width: 200px;
}

.side-bar table td {
  width: 50%;
  padding: 4px;
}

nav {
  margin: 40px 40px 0px 6%;
}

nav a {
  border: 1px solid #969696;
  background: #aaaaaa;
  padding: 20px;
  text-decoration: none;
  border-radius: 10px 10px 0 0;
  color: black;
}

nav a.active {
  background: #e4dcc4;
  border: 1px solid #e4dcc4;
}

.ID-form {
  position: absolute;
  top: 10px;
  left: 35%;
}

.new-document {
  width: 60%;
  margin: 20px auto;
}

.new-document input {
  width: 300px;
  margin: 0 20px 30px;
  float: right;
}

.new-document input[type="submit"] {
  width: 300px;
  margin: auto;
}

.new-document select {
  margin-left: 160px;
  width: 310px;
}

.new-document textarea {
  margin: 0 20px 20px 0;
  width: 310px;
  height: 100px;
  float: right;
}

.new-document .document-form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.attachment-form {
  width: 50%;
  margin: 40px auto;
  display: grid;
  -webkit-justify-content: center;
          justify-content: center;
}

.attachment-form input[type="text"],
.attachment-form input[type="date"] {
  float: right;
  margin: 0 0 30px 40px;
  width: 300px;
}

.lab-order-form {
  display: grid;
  -webkit-justify-content: center;
          justify-content: center;
}

.lab-order-form .tests {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  -webkit-justify-content: center;
          justify-content: center;
}

h2 {
  text-align: center;
}

.lab-container {
  text-align: center;
  width: 70%;
  margin: 30px auto !important;
  height: 70vh;
}

.lab-container form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 60%;
  margin: 0 auto;
}

.lab-container form .inner-form input[type="text"], .lab-container form .inner-form input[type="date"],
.lab-container form label input[type="text"],
.lab-container form label input[type="date"] {
  width: 150px;
  margin-top: 10px;
}

.lab-container .table {
  width: 90%;
  margin: 0 auto;
}

.lab-container .table table {
  width: 70%;
  text-align: left;
  margin: auto;
}

.lab-container .table table td {
  margin: 10px;
}

